 <template>
   <div class="skill md-primary">
     <div class="row" :class="[classes]">
       <div class="col-sm-4">
          <h3 class="name">
            {{ name }}
          </h3>
       </div>
       <div class="col-sm-7">
          <md-rating-bar class="md-primary rating" v-model="rating" :md-num-stars="10" disabled></md-rating-bar>
       </div>
     </div>
   </div>
 </template>

 <style lang="scss">
 @import "./css/media-queries-breakpoints";
 @import "./css/utils";

 .skill{
   display: block;
   padding: 4px 6px;
   @include border-radius;

   &:hover{
      background: rgba(50, 50, 50, .1);
   }

   @include xs{
     width: fit-content;
     margin: 0px auto;
   }

   .name, .md-rating-bar{
     display: inline-flex;
   }

   .name{
     margin: 0em 1em 0em 0em;
     font-weight: normal;
   }

   .md-rating-bar.rating{
     margin-top: auto;
     margin-bottom: auto;

     @include xs{
         margin-left: 4em;
     }

     .md-back-stars, .md-front-stars{
       display: inline-flex !important;
     }
   }
 }
 </style>

 <script>
 export default{
   name: 'skill',
   props: {
     rating: {
       type: Number,
       default: 0,
     },
     name: {
       type: String,
       default: '',
     },
     break: {
       type: String,
       default: 'auto',
     },
   },
   data: function(){
     return {};
   },
   computed: {
     classes: function(){
       let classes = {};

       let validsRowEdH = ['sm', 'xs'];
       let rowEqH = 'row-eq-h';
       if(validsRowEdH.indexOf(this.break) > -1)
         rowEqH += `-${this.break}`;
       classes[rowEqH] = true;

       return classes;
     },
   },
 };
 </script>



